import cx from "classnames";
import React, { useCallback, useMemo } from "react";
import { useScrollVisible } from "../../../../common/util/useScrollVisible";

import { Block as BlockType, Dispatch } from "../types";
import { blockIsTable } from "../util";
import { Editor } from "./Editor";
import { useScrollToActiveTextBlock } from "./useScrollToActiveTextBlock";

type Props = Readonly<{
  active: boolean;
  block: BlockType;
  dispatch: Dispatch;
  scrollRef: HTMLElement;
  scrollTo: boolean;
}>;

const isBlockInvalid = (block: BlockType): boolean => {
  if (blockIsTable(block)) {
    return block.cells.some(r => r.some(c => !c.validXml));
  }
  return !block.validXml;
};

export const Block = ({ active, block, dispatch, scrollRef, scrollTo }: Props) => {
  const [ref, setRef, isVisible] = useScrollVisible(scrollRef, "0px");
  const onClick = useCallback(() => {
    if (active) {
      return;
    }
    dispatch({
      blockId: block.id,
      type: "MARK BLOCK ACTIVE",
    });
  }, [active, block.id, dispatch]);
  useScrollToActiveTextBlock(scrollRef, ref, scrollTo);
  const editor = useMemo(() => {
    if (!isVisible) {
      return undefined;
    }
    return <Editor block={block} disabled={!active} dispatch={dispatch} />;
  }, [active, block, dispatch, isVisible]);
  const blockClassName = cx("block", {
    invalid: isBlockInvalid(block),
  });
  return (
    <div className={blockClassName} onClick={onClick} ref={setRef}>
      {editor}
    </div>
  );
};
