import { MousePosition, State } from "../../types";
import { isMouseOnPage } from "../util";

export type MouseDownAction = Readonly<{
  ctrlClick: boolean;
  position: MousePosition;
  type: "MOUSE DOWN";
  shiftClick: boolean;
}>;

export const mouseDown = (state: State, action: MouseDownAction): State => {
  const { ctrlClick, position, shiftClick } = action;
  if (isMouseOnPage(position)) {
    const selectedBlockId = Object.keys(state.blocks).find(k => {
      const block = state.blocks[k];
      return (
        position.page === block.page &&
        position.x >= block.minX &&
        position.x <= block.maxX &&
        position.y >= block.minY &&
        position.y <= block.maxY
      );
    });
    if (!selectedBlockId) {
      if (ctrlClick || shiftClick) {
        return state;
      }
      return {
        ...state,
        selectedBlockId: undefined,
      };
    }
    let blocks = state.blocks;
    if (!state.readonly && state.selectedBlockId && shiftClick) {
      const selectedBlock = blocks[selectedBlockId];
      const previousSelectedBlock = blocks[state.selectedBlockId];
      if (selectedBlock.continuationId !== "" || selectedBlock.continuedFromId !== "") {
        const continuedBlockId = selectedBlock.continuationId !== "" ?
        selectedBlock.continuationId : selectedBlock.continuedFromId;
        blocks[selectedBlockId] = {
          ...blocks[selectedBlockId],
          continuationId: "",
          continuedFromId: "",
        };
        blocks[continuedBlockId] = {
          ...blocks[continuedBlockId],
          continuationId: "",
          continuedFromId: "",
        };
      }
      else if (state?.selectedBlockId !== selectedBlockId && previousSelectedBlock.continuationId === "" && previousSelectedBlock.continuedFromId === "")
      {
        blocks[previousSelectedBlock.id] = {
          ...blocks[previousSelectedBlock.id],
          continuationId: blocks[selectedBlockId].id,
        };
        blocks[selectedBlockId] = {
          ...blocks[selectedBlockId],
          continuedFromId: previousSelectedBlock.id,
        };
      }
    }
    if (!state.readonly && state.showReadingOrder && ctrlClick && state.selectedBlockId) {
      const selectedIndex = state.blockOrder.indexOf(selectedBlockId);
      const currentIndex = state.blockOrder.indexOf(state.selectedBlockId);
      if (selectedIndex >= 0 && currentIndex >= 0) {
        let blockOrder = state.blockOrder;
        if (selectedIndex === currentIndex) {
          blockOrder = [
            state.blockOrder[selectedIndex],
            ...state.blockOrder.slice(0, selectedIndex),
            ...state.blockOrder.slice(selectedIndex + 1),
          ];
        } else if (selectedIndex > currentIndex) {
          blockOrder = [
            ...state.blockOrder.slice(0, currentIndex),
            state.blockOrder[currentIndex],
            state.blockOrder[selectedIndex],
            ...state.blockOrder.slice(currentIndex + 1, selectedIndex),
            ...state.blockOrder.slice(selectedIndex + 1),
          ];
        } else {
          blockOrder = [
            ...state.blockOrder.slice(0, selectedIndex),
            ...state.blockOrder.slice(selectedIndex + 1, currentIndex),
            state.blockOrder[currentIndex],
            state.blockOrder[selectedIndex],
            ...state.blockOrder.slice(currentIndex + 1),
          ];
        }
        return {
          ...state,
          blockOrder,
          scrollToTextBlock: selectedBlockId,
          selectedBlockId,
        };
      }
    }
    return {
      ...state,
      blocks,
      scrollToTextBlock: selectedBlockId,
      selectedBlockId,
    };
  }
  if (state.selectedBlockId) {
    return {
      ...state,
      selectedBlockId: undefined,
    };
  }
  return state;
};
