import cx from "classnames";
import React, { useCallback, useEffect, useState } from "react";
import { Input } from "reactstrap";
import parse from 'html-react-parser';

import { Dispatch, TextBlock } from "../../types";

type Props = Readonly<{
  block: TextBlock;
  disabled: boolean;
  dispatch: Dispatch;
}>;

export const TextEditor = ({ block, disabled, dispatch }: Props) => {
  const [ref, setRef] = useState<HTMLTextAreaElement | null>(null);
  const onChange = useCallback(
    e => {
      dispatch({
        blockId: block.id,
        text: e.target.value,
        type: "UPDATE BLOCK TEXT",
      });
    },
    [block.id, dispatch],
  );
  useEffect(() => {
    if (!ref || disabled) {
      return;
    }
    ref.focus();
    setTimeout(() => {
      ref.setSelectionRange(ref.value.length, ref.value.length);
      ref.scrollTop = ref.scrollHeight;
    }, 0);
  }, [disabled, ref]);
  const editorWrapperClassName = cx("editor");
  const editorClassName = cx("editor-text", {
    invalid: !block.validXml,
  });
  return (
    <div className={editorWrapperClassName}>
      {parse(block.text)}
      <Input
        className={editorClassName}
        disabled={disabled}
        innerRef={setRef as any}
        onChange={onChange}
        type="textarea"
        value={block.text}
      />
    </div>
  );
};
TextEditor.displayName = "TextEditor";
