import React, { useEffect, useMemo, useState } from "react";

import { Range, State } from "../../types";
import { drawArrowHead } from "./drawArrowHead";

type Props = Readonly<{
  gutterWidth: number;
  height: number;
  pageRanges: ReadonlyArray<Range>;
  readyPages: Array<boolean>;
  scale: number;
  showReadingOrder: boolean;
  state: State;
  width: number;
}>;

export const Blocks = ({
  gutterWidth,
  height,
  pageRanges,
  readyPages,
  scale,
  showReadingOrder,
  state,
  width,
}: Props) => {
  const canvasStyles = useMemo(
    () => ({
      height: `${height}px`,
      width: `${width}px`,
    }),
    [height, width],
  );
  const [canvasRef, setCanvasRef] = useState<HTMLCanvasElement | null>(null);
  useEffect(() => {
    if (!canvasRef) {
      return;
    }
    const context = canvasRef.getContext("2d");
    if (!context) {
      return;
    }
    context.clearRect(0, 0, canvasRef.width, canvasRef.height);
    context.lineWidth = 2.5;
    for (let i = 0; i < state.blockOrder.length; i++) {
      const block = state.blocks[state.blockOrder[i]];
      if (!readyPages[block.page]) {
        continue;
      }
      const blockX = block.minX * scale + gutterWidth;
      const blockY = block.minY * scale + pageRanges[block.page].start;
      const blockWidth = (block.maxX - block.minX) * scale;
      const blockHeight = (block.maxY - block.minY) * scale;
      if (state.selectedBlockId === block.id || state.selectedBlockId === block.continuationId || state.selectedBlockId === block.continuedFromId) {
        context.fillStyle = "rgba(0, 0, 255, 0.3)";
        context.fillRect(blockX, blockY, blockWidth, blockHeight);
      } else {
        context.strokeStyle = "rgba(0, 0, 255, 0.5)";
        context.strokeRect(blockX, blockY, blockWidth, blockHeight);
      }
      if (showReadingOrder && i < state.blockOrder.length - 1) {
        const startX = ((block.maxX + block.minX) / 2) * scale + gutterWidth;
        const startY = ((block.maxY + block.minY) / 2) * scale + pageRanges[block.page].start;
        const nextBlock = state.blocks[state.blockOrder[i + 1]];
        const endX = ((nextBlock.maxX + nextBlock.minX) / 2) * scale + gutterWidth;
        const endY =
          ((nextBlock.maxY + nextBlock.minY) / 2) * scale + pageRanges[nextBlock.page].start;
        context.fillStyle = "rgba(255, 0, 0, 0.5)";
        context.strokeStyle = "rgba(255, 0, 0, 0.5)";
        context.beginPath();
        context.moveTo(startX, startY);
        context.lineTo(endX, endY);
        context.stroke();

        context.beginPath();
        context.arc(startX, startY, 6, 0, 2 * Math.PI, false);
        context.arc(endX, endY, 6, 0, 2 * Math.PI, false);
        context.fill();
        drawArrowHead(
          context,
          { x: startX, y: startY },
          { x: (startX + endX) / 2, y: (startY + endY) / 2 },
          8,
        );
      }
      if (block.continuationId !== ""){
        const continuationBlock = state.blocks[block.continuationId];
        const startX = ((block.maxX + block.minX) / 2) * scale + gutterWidth;
        const startY = ((block.maxY + block.minY) / 2) * scale + pageRanges[block.page].start;
        const endX = ((continuationBlock.maxX + continuationBlock.minX) / 2) * scale + gutterWidth;
        const endY = ((continuationBlock.maxY + continuationBlock.minY) / 2) * scale + pageRanges[continuationBlock.page].start;
        context.fillStyle = "rgba(255, 255, 0)";
        context.strokeStyle = "rgba(255, 255, 0)";
        context.beginPath();
        context.moveTo(startX, startY);
        context.lineTo(endX, endY);
        context.stroke();
      }
    }
  });
  return (
    <canvas
      className="blocks"
      height={height}
      ref={setCanvasRef}
      style={canvasStyles}
      width={width}
    />
  );
};
Blocks.displayName = "Background";
