import { ReactElement, useMemo, useReducer } from "react";

import { Block, Dispatch, State } from "../types";
import { reducer } from "./reducer";

type ChildrenArgs = Readonly<{
  dispatch: Dispatch;
  state: State;
}>;
type Props = Readonly<{
  children: (args: ChildrenArgs) => ReactElement;
  initialBlocks: ReadonlyArray<Block>;
  readonly: boolean;
}>;

export const StateManager = ({ children, initialBlocks, readonly }: Props) => {
  const initialState = useMemo((): State => {
    const blocks: { [blockId: string]: Block } = {};
    for (const block of initialBlocks) {
      blocks[block.id] = block;
    }
    return {
      blockOrder: Object.keys(blocks),
      blocks,
      readonly,
      scrollToPageBlock: undefined,
      scrollToTextBlock: undefined,
      selectedBlockId: undefined,
      showReadingOrder: true,
    };
  }, [initialBlocks]);
  const [state, dispatch] = useReducer(reducer, initialState);
  const args = useMemo((): ChildrenArgs => ({ dispatch, state }), [dispatch, state]);
  return children(args);
};
