import cx from "classnames";
import React, { useCallback } from "react";
import { Input } from "reactstrap";

import { Dispatch, TableCell as TableCellType } from "../../types";

type Props = Readonly<{
  blockId: string;
  cell: TableCellType;
  disabled: boolean;
  dispatch: Dispatch;
}>;

export const TableCell = ({ blockId, cell, disabled, dispatch }: Props) => {
  const onChange = useCallback(
    e => {
      dispatch({
        blockId,
        cellId: cell.id,
        text: e.target.value,
        type: "UPDATE CELL TEXT",
      });
    },
    [cell.id, dispatch],
  );
  const editorClassName = cx("editor-text", {
    invalid: !cell.validXml,
  });
  return (
    <Input
      className={editorClassName}
      disabled={disabled}
      onChange={onChange}
      placeholder="(No text)"
      type="textarea"
      value={cell.text}
    />
  );
};
TableCell.displayName = "TableCell";
