import React from "react";

import { Block, Dispatch } from "../../types";
import { blockIsTable } from "../../util";
import { TableEditor as TableEditorComponent } from "./TableEditor";
import { TextEditor as TextEditorComponent } from "./TextEditor";

type Props = Readonly<{
  block: Block;
  disabled: boolean;
  dispatch: Dispatch;
}>;

const TableEditor = React.memo(TableEditorComponent);
const TextEditor = React.memo(TextEditorComponent);

export const Editor = ({ block, disabled, dispatch }: Props) => {
  if (blockIsTable(block)) {
    return <TableEditor block={block} disabled={disabled} dispatch={dispatch} />;
  }
  return <TextEditor block={block} disabled={disabled} dispatch={dispatch} />;
};
