import { JobEvent, JobStatus, ListedJob } from "@common/api/v1.1/types/jobs";
import { JobState, State } from "../types";

export interface UpdateJobsAction {
  blockingStep: string;
  eventType: JobEvent | null;
  jobs: Array<ListedJob>;
  type: "UPDATE JOBS";
}

export const updateJobs = (state: State, action: UpdateJobsAction): State => {
  if (action.jobs.length < 1) {
    return state;
  }
  if (action.eventType === null) {
    return {
      ...state,
      jobs: action.jobs.reduce((prev: State["jobs"], j: ListedJob): State["jobs"] => {
        prev[j.id] = {
          data: j,
          override: undefined,
        };
        return prev;
      }, {}),
    };
  }
  let jobs = state.jobs;
  switch (action.eventType) {
    case JobEvent.JobBlocked:
    case JobEvent.JobCheckedOut:
    case JobEvent.JobCollaborationChanged:
    case JobEvent.JobCreated:
    case JobEvent.JobProgressChanged:
    case JobEvent.JobStatusChanged:
      jobs = action.jobs.reduce((prev: State["jobs"], j: ListedJob): State["jobs"] => {
        if (j.status === JobStatus.BLOCKED && j.blocking_step === action.blockingStep) {
          const newJobData = {
            data: j,
            override: state.jobs[j.id]?.override,
          };
          if (prev === state.jobs) {
            return {
              ...state.jobs,
              [j.id]: newJobData,
            };
          }
          prev[j.id] = newJobData;
        } else if (j.id in prev) {
          let toReturn = prev;
          if (prev === state.jobs) {
            toReturn = { ...prev };
          }
          delete toReturn[j.id];
          return toReturn;
        }
        return prev;
      }, state.jobs);
      break;
    case JobEvent.JobCheckedIn:
    case JobEvent.JobCancelled:
    case JobEvent.JobDeleted:
    case JobEvent.JobFailed:
      jobs = action.jobs.reduce((prev: State["jobs"], j: ListedJob): State["jobs"] => {
        if (!(j.id in prev)) {
          return prev;
        }
        if (prev === state.jobs) {
          const toReturn = { ...prev };
          delete toReturn[j.id];
          return toReturn;
        }
        delete prev[j.id];
        return prev;
      }, state.jobs);
      break;
  }
  if (jobs === state.jobs) {
    return state;
  }
  // if the current job has been removed from the database, we need to clear the current job state
  let job = state.job;
  if (
    (job.state === JobState.ready && !(job.job.id in jobs)) ||
    ((job.state === JobState.loading ||
      job.state === JobState.aborted ||
      job.state === JobState.failed) &&
      !(job.jobId in jobs))
  ) {
    job = { state: JobState.initial };
  }
  return {
    ...state,
    job,
    jobs,
  };
};
