import { Action, State } from "../../types";
import { markBlockActive } from "./markBlockActive";
import { mouseDown } from "./mouseDown";
import { reOrderBlock } from "./reOrderBlock";
import { toggleShowReadingOrder } from "./toggleShowReadingOrder";
import { updateBlockText } from "./updateBlockText";
import { updateCellText } from "./updateCellText";

export const reducer = (state: State, action: Action): State => {
  switch (action.type) {
    case "MARK BLOCK ACTIVE":
      return markBlockActive(state, action);
    case "MOUSE DOWN":
      return mouseDown(state, action);
    case "REORDER BLOCK":
      return reOrderBlock(state, action);
    case "TOGGLE SHOW READING ORDER":
      return toggleShowReadingOrder(state, action);
    case "UPDATE BLOCK TEXT":
      return updateBlockText(state, action);
    case "UPDATE CELL TEXT":
      return updateCellText(state, action);
  }
};
