import { JobState, State } from "../types";

export interface ClearActiveJobAction {
  type: "CLEAR ACTIVE JOB";
}
export const clearActiveJob = (state: State, _: ClearActiveJobAction): State => {
  if (state.job.state === JobState.initial && state.sidebarVisible === true) {
    return state;
  }
  return {
    ...state,
    job: {
      state: JobState.initial,
    },
    sidebarVisible: true,
  };
};
