import cx from "classnames";
import React from "react";
import { Table } from "reactstrap";

import { Dispatch, TableBlock } from "../../types";
import { TableCell as TableCellComponent } from "./TableCell";

type Props = Readonly<{
  block: TableBlock;
  disabled: boolean;
  dispatch: Dispatch;
}>;

const TableCell = React.memo(TableCellComponent);

export const TableEditor = ({ block, disabled, dispatch }: Props) => {
  const editorClass = cx("editor", "table", {
    disabled,
  });
  return (
    <div className={editorClass}>
      <Table>
        <tbody>
          {block.cells.map((r, i) => (
            <tr key={`row-${i}`}>
              {r.map((c, j) => (
                <td colSpan={c.colSpan} key={`cell-${j}`} rowSpan={c.rowSpan}>
                  <TableCell blockId={block.id} cell={c} disabled={disabled} dispatch={dispatch} />
                </td>
              ))}
            </tr>
          ))}
        </tbody>
      </Table>
    </div>
  );
};
TableEditor.displayName = "TableEditor";
