import React, { useCallback, useMemo, useState } from "react";

import { Dispatch, Image, Range, State } from "../types";
import { Background as BackgroundComponent } from "./Background";
import { Blocks } from "./Blocks";
import { InputHandler } from "./InputHandler";
import { useResizableElement } from "./useResizableElement";
import { useScrollToActivePageBlock } from "./useScrollToActivePageBlock";

type Props = Readonly<{
  dispatch: Dispatch;
  gutterWidth: number;
  images: ReadonlyArray<Image>;
  state: State;
}>;

const Background = React.memo(BackgroundComponent);

export const PageView = ({ dispatch, gutterWidth, images, state }: Props) => {
  const [ref, setRef, height, width] = useResizableElement<HTMLDivElement>();
  const [readyPages, setReadyPages] = useState<Array<boolean>>([]);
  const markPageReady = useCallback(
    (page: number) => {
      setReadyPages(p => {
        if (p[page]) {
          return p;
        }
        return [...p.slice(0, page), true, ...p.slice(page + 1)];
      });
    },
    [setReadyPages],
  );
  const scale = useMemo(() => {
    const maxPageWidth = Math.max(...images.map(i => i.width));
    return (width - gutterWidth * 2) / maxPageWidth;
  }, [gutterWidth, height, images, width]);
  const pageRanges: ReadonlyArray<Range> = useMemo((): Array<Range> => {
    const offsets: Array<Range> = [];
    let offset = gutterWidth;
    for (const image of images) {
      offsets.push({
        end: offset + image.height * scale,
        start: offset,
      });
      offset += image.height * scale + gutterWidth;
    }
    return offsets;
  }, [gutterWidth, images, scale]);
  const canvasHeight = useMemo(() => {
    return (
      images.reduce((acc, next) => acc + next.height, 0) * scale +
      gutterWidth * images.length +
      gutterWidth
    );
  }, [gutterWidth, images, scale]);
  useScrollToActivePageBlock(
    ref,
    state.scrollToPageBlock ? state.blocks[state.scrollToPageBlock] : undefined,
    scale,
    pageRanges,
  );
  return (
    <div className="page" ref={setRef}>
      {ref && height && width && (
        <>
          <Background
            gutterWidth={gutterWidth}
            height={canvasHeight}
            images={images}
            markPageReady={markPageReady}
            pageRanges={pageRanges}
            readyPages={readyPages}
            scale={scale}
            width={width}
          />
          <Blocks
            gutterWidth={gutterWidth}
            height={canvasHeight}
            pageRanges={pageRanges}
            readyPages={readyPages}
            scale={scale}
            showReadingOrder={state.showReadingOrder}
            state={state}
            width={width}
          />
          <InputHandler
            dispatch={dispatch}
            gutterWidth={gutterWidth}
            height={canvasHeight}
            pageRanges={pageRanges}
            scale={scale}
          />
        </>
      )}
    </div>
  );
};
