import xmldoc, { XmlCDataNode, XmlElement, XmlNode, XmlTextNode } from "xmldoc";
import { isValidXml } from "../Reading/StateManager/util";
import { TableBlock, TableCell } from "../Reading/types";

const ATTR_XMLNS = "xmlns";
const VALUE_XMLNS = "http://innodatalabs.com/innodom";

const childNodeIsElement = (c: XmlNode): c is XmlElement => c.type === "element";
const childNodeIsText = (c: XmlNode): c is XmlTextNode => c.type === "text";
const childNodeIsCharData = (c: XmlNode): c is XmlCDataNode => c.type === "cdata";
const getAttributesString = (c: XmlElement): string => {
  const attributes = Object.keys(c.attr);
  if (attributes.length < 1) {
    return "";
  }
  return ` ${attributes.map(a => `${a}="${c.attr[a].replace(/\"/g, '\\"')}"`).join(" ")}`;
};
const escapeXmlEntities = (input: string): string =>
  input
    .replace(/&/g, "&amp;")
    .replace(/</g, "&lt;")
    .replace(/>/g, "&gt;");
const mapNodeToHtml = (el: XmlNode): string => {
  if (!childNodeIsElement(el)) {
    if (childNodeIsCharData(el)) {
      return escapeXmlEntities(el.cdata);
    } else if (childNodeIsText(el)) {
      return escapeXmlEntities(el.text);
    }
    return "";
  }
  const hasChildren = el.children.length > 0;
  if (!hasChildren) {
    return `<${el.name}${getAttributesString(el)} />`;
  }
  const contents = el.children.map(mapNodeToHtml).join("");
  return `<${el.name}${getAttributesString(el)}>${contents}</${el.name}>`;
};

export const convertInnoTextToReadingHTML = (input: string): string => {
  try {
    const xmlDoc = new xmldoc.XmlDocument(input);
    if (xmlDoc.name !== "text") {
      throw new Error("Invalid Innodom.");
    }
    return xmlDoc.children
      .map(mapNodeToHtml)
      .join("")
      .trim();
  } catch (err) {
    console.error(err);
    throw err;
  }
};

export const convertReadingHTMLToInnodom = (input: string): string => {
  try {
    const xmlDoc = document.implementation.createDocument(null, null, null);
    const textEl = xmlDoc.createElement("text");
    textEl.innerHTML = input;
    return `<text ${ATTR_XMLNS}="${VALUE_XMLNS}">${textEl.innerHTML}</text>`;
  } catch (err) {
    console.error(err);
    throw new Error("Invalid XML generated.");
  }
};

export const convertReadingTableBlockToInnodom = (input: TableBlock): string => {
  try {
    const xmlDoc = document.implementation.createDocument(null, null, null);
    const table = xmlDoc.createElement("table");
    for (const row of input.cells) {
      const rowEl = xmlDoc.createElement("tr");
      for (const cell of row) {
        const cellEl = xmlDoc.createElement("td");
        for (const key of Object.keys(cell.attributes)) {
          cellEl.setAttribute(key, cell.attributes[key]);
          cellEl.innerHTML = cell.text;
        }
        rowEl.appendChild(cellEl);
      }
      table.appendChild(rowEl);
    }
    return `<table ${ATTR_XMLNS}="${VALUE_XMLNS}">${table.innerHTML}</table>`;
  } catch (err) {
    console.error(err);
    throw new Error("Invalid XML generated.");
  }
};

const extractSpan = (attr: string) => (col: XmlElement): number => {
  for (const key of Object.keys(col.attr)) {
    const cleanKey = key.trim().toLowerCase();
    if (cleanKey === attr) {
      const parsedSpan = Math.max(1, parseInt(col.attr[key], 10));
      if (!isNaN(parsedSpan)) {
        return parsedSpan;
      }
    }
  }
  return 1;
};
const extractColSpan = extractSpan("colspan");
const extractRowSpan = extractSpan("rowspan");

export const extractTableCells = (
  tableXml: string | undefined,
  blockId: string,
): TableBlock["cells"] => {
  if (typeof tableXml !== "string") {
    throw new Error("Missing table xml.");
  }
  const xmlDoc = new xmldoc.XmlDocument(tableXml);
  return xmlDoc.childrenNamed("tr").map(
    (row: XmlElement, rowIndex: number): Array<TableCell> =>
      row.childrenNamed("td").map(
        (col: XmlElement, colIndex: number): TableCell => {
          const text = col.children.map(mapNodeToHtml).join("");
          return {
            attributes: col.attr,
            colSpan: extractColSpan(col),
            id: `${blockId}-row-${rowIndex + 1}-col-${colIndex + 1}`,
            rowSpan: extractRowSpan(col),
            text,
            validXml: isValidXml(text),
          };
        },
      ),
  );
};
