import { useCallback, useEffect, useMemo, useState } from "react";
import { useSSE } from "react-hooks-sse";

import { JobsApi } from "@common/api/v1.1/actions/JobsApi";
import { isJobEvent, ListedJob } from "@common/api/v1.1/types/jobs";
import { Actions, State } from "../../../types";

export enum JobListState {
  failed,
  fetching,
  ready,
}
type JobListStateResponse = [
  JobListState, // current state
  Array<string>, // list of job IDs for the current page
  number, // the current page number
  number, // the total number of pages
  number, // the total nubmer of jobs
  string, // the current text filter
  () => void, // nextPage callback
  () => void, // previousPage callback
  () => void, // retry failed page callback
  (text: string) => void, // callback to update text filter
];

const JOBS_PER_PAGE = 10;

const fetchJobs = async (auth: State["auth"], blockingTask: string): Promise<Array<ListedJob>> => {
  let jobs: Array<ListedJob> = [];
  let totalCount = -1;
  const jobsApi = new JobsApi(auth);
  do {
    const result = await jobsApi.listJobs(100, jobs.length, { blockingTask, status: "blocked" });
    if (result.total_count === 0) {
      return [];
    }
    if (totalCount < 0) {
      totalCount = result.total_count;
      jobs = result.list;
      continue;
    }
    if (totalCount !== result.total_count) {
      // total count changed as we were fetching, so start over
      totalCount = -1;
      jobs = [];
      continue;
    }
    jobs = jobs.concat(result.list);
  } while (totalCount < 0 || jobs.length < totalCount);
  return jobs;
};

export const useJobListState = (
  auth: State["auth"],
  blockingStep: string,
  jobDatabase: State["jobs"],
  updateJobs: Actions["updateJobs"],
): JobListStateResponse => {
  const [pageNumber, setPageNumber] = useState(0);
  const [jobListState, setJobListState] = useState<JobListState>(JobListState.fetching);
  const [jobIds, setJobIds] = useState<Array<string>>([]);
  const [textFilter, setTextFilter] = useState("");
  useEffect(() => {
    const databaseJobIds = Object.keys(jobDatabase);
    if (databaseJobIds.length !== jobIds.length) {
      setJobIds(databaseJobIds);
    }
  }, [jobDatabase]);
  useEffect(() => {
    setPageNumber(0);
    setJobListState(JobListState.fetching);
    setJobIds([]);
  }, [auth]);
  useEffect(() => {
    if (jobListState !== JobListState.fetching) {
      return;
    }
    fetchJobs(auth, blockingStep)
      .then(jobs => {
        updateJobs(jobs, null);
        setJobIds(jobs.map(j => j.id));
        setJobListState(JobListState.ready);
      })
      .catch(err => {
        console.error(err);
        setJobIds([]);
        setJobListState(JobListState.failed);
      });
  }, [jobListState]);
  useSSE(
    "jobEvent",
    {},
    {
      parser: input => JSON.parse(input),
      stateReducer: (state, action) => {
        if (action.data) {
          const parsedData: any = action.data;
          const { job, type } = parsedData;
          if (typeof job === "object" && job !== null && !Array.isArray(job) && isJobEvent(type)) {
            updateJobs([job], type);
            return state;
          }
          console.error("Invalid event data:", action.data);
        }
        return state;
      },
    },
  );
  const filteredList = useMemo(() => {
    const clean = textFilter.trim().toLowerCase();
    if (clean.length < 1) {
      return jobIds;
    }
    return jobIds.filter(id => {
      if (id.trim().toLowerCase() === clean) {
        return true;
      } else if (!(id in jobDatabase)) {
        return false;
      }
      const job = jobDatabase[id];
      const split = job.data.name.split(" ").map(s => s.trim().toLowerCase());
      return split.some(s => s.startsWith(clean));
    });
  }, [jobIds, textFilter]);
  const totalPages = Math.ceil(filteredList.length / JOBS_PER_PAGE);
  const nextPage = useCallback(() => {
    if (pageNumber >= totalPages - 1) {
      return;
    }
    setPageNumber(pageNumber + 1);
  }, [pageNumber, setPageNumber, totalPages]);
  const prevPage = useCallback(() => {
    if (pageNumber <= 0) {
      return;
    }
    setPageNumber(pageNumber - 1);
  }, [pageNumber, setPageNumber]);
  const currentJobIds = useMemo(
    () => filteredList.slice(pageNumber * JOBS_PER_PAGE, (pageNumber + 1) * JOBS_PER_PAGE),
    [filteredList, pageNumber, JOBS_PER_PAGE],
  );
  const retry = useCallback(() => {
    setJobListState(JobListState.fetching);
  }, [setJobListState]);
  return [
    jobListState,
    currentJobIds,
    pageNumber,
    totalPages,
    filteredList.length,
    textFilter,
    nextPage,
    prevPage,
    retry,
    setTextFilter,
  ];
};
